<template>
    <div id="map" ref="mapContainer" class="map"></div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { Loader } from '@googlemaps/js-api-loader';
import MarkerClusterer from '@googlemaps/markerclustererplus';

export default {
    name: 'GoogleMap',
    setup() {
        const mapContainer = ref(null);
        let currentInfoWindow = null;
        let markerCluster = null;
        let markers = [];
        let userLocation = null;

        onMounted(() => {
            const loader = new Loader({
                apiKey: process.env.VUE_APP_GOOGLE_MAPS_API_KEY,
                version: 'weekly',
            });

            loader.load().then(() => {
                const map = new google.maps.Map(mapContainer.value, {
                    center: { lat: 25.0330, lng: 121.5654 },
                    zoom: 12
                });

                fetch('youbike_data.csv')
                    .then(response => response.text())
                    .then(data => {
                        const rows = data.trim().split('\n').slice(1);
                        rows.forEach(row => {
                            const columns = row.split(',');
                            const sna = columns[1];
                            const lat = parseFloat(columns[6]);
                            const lng = parseFloat(columns[7]);
                            const availableBikes = parseInt(columns[3]);
                            const total = parseInt(columns[2]);
                            const act = parseInt(columns[13]);
                            const infoTime = columns[16];

                            let icon = './normal.ico';

                            if (availableBikes === 0) {
                                icon = './no_bike.ico';
                            } else if (availableBikes === total) {
                                icon = './full.ico';
                            } else if (act === 0) {
                                icon = './closed.ico';
                            }

                            const marker = new google.maps.Marker({
                                position: { lat, lng },
                                map,
                                title: sna,
                                icon: {
                                    url: `/${icon}`,
                                    scaledSize: new google.maps.Size(40, 40)
                                }
                            });

                            const content = `
                                <div>
                                    <p>租賃站點查詢: ${sna}</p>
                                    <p>站點位置: ${columns[8]}</p>
                                    <p>可借車輛: ${availableBikes}</p>
                                    <p>可停空位: ${parseInt(columns[12])}</p>
                                    <p>時間: ${infoTime}</p>
                                </div>
                            `;
                            const infoWindow = new google.maps.InfoWindow({
                                content: content
                            });

                            marker.addListener('click', () => {
                                if (currentInfoWindow) {
                                    currentInfoWindow.close();
                                }
                                infoWindow.open(map, marker);
                                currentInfoWindow = infoWindow;
                            });

                            markers.push(marker);
                        });

                        markerCluster = new MarkerClusterer(map, markers, {
                            minimumClusterSize: 8,
                            imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m'
                        });

                        map.addListener('zoom_changed', () => {
                            const currentZoom = map.getZoom();
                            if (currentZoom <= 12) {
                                markerCluster.setMaxZoom(12);
                            } else {
                                markerCluster.setMaxZoom(null);
                            }
                        });

                    })
                    .catch(error => {
                        console.error('Error fetching CSV:', error);
                    });

                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(
                        position => {
                            userLocation = {
                                lat: position.coords.latitude,
                                lng: position.coords.longitude
                            };

                            new google.maps.Marker({
                                position: userLocation,
                                map,
                                title: 'Your Location',
                                icon: {
                                    url: './user_location.ico',
                                    scaledSize: new google.maps.Size(40, 40)
                                }
                            });

                            map.setCenter(userLocation);

                            let closestMarker = null;
                            let closestDistance = Infinity;

                            markers.forEach(marker => {
                                const distance = google.maps.geometry.spherical.computeDistanceBetween(
                                    new google.maps.LatLng(userLocation.lat, userLocation.lng),
                                    marker.getPosition()
                                );

                                if (distance < closestDistance) {
                                    closestDistance = distance;
                                    closestMarker = marker;
                                }
                            });

                            if (closestMarker) {
                                const content = `
                                    <div>
                                        <p>最近的租賃站點: ${closestMarker.getTitle()}</p>
                                        <p>距離: ${(closestDistance / 1000).toFixed(2)} 公里</p>
                                    </div>
                                `;
                                const closestInfoWindow = new google.maps.InfoWindow({
                                    content: content
                                });
                                closestInfoWindow.open(map, closestMarker);
                                currentInfoWindow = closestInfoWindow;
                            }
                        },
                        error => {
                            console.error('Error getting user location:', error);
                        }
                    );
                }
            });
        });

        return {
            mapContainer
        };
    }
};
</script>

<style scoped>
.map {
    height: 80vh;
    width: 80vw;
    margin: auto;
}
</style>
